import React, { useEffect, useState } from 'react';

const StatusIndicator = ({ value, threshold1, threshold2, threshold3 }) => {
  const [color, setColor] = useState('');

  useEffect(() => {
    if (value >= threshold3) {
      setColor('bg-red-500');
    }
    if (value == threshold3) {
      setColor('bg-orange-500');
      if (value > threshold3 && value <= threshold2) {
        setColor('bg-orange-500');
      } if (value > threshold2 && value <= threshold1) {
        setColor('bg-green-500');
      }
    }
  }, [value, threshold1, threshold2, threshold3]);

  return (
    <div className={`w-full h-5 rounded-2xl ${color} mb-4`} />
  );
};

export default StatusIndicator;