import React from 'react';
import { useTranslation } from 'react-i18next';

const Sidebar = ({ isOpen, toggleSidebar }) => {

  const { t } = useTranslation(); // Add translation hook



  const swithTemperatureMeting = () => {
    console.log("switch mode")
  }
  return (
    <div>
      {/* Button to toggle the sidebar */}


      {/* Sidebar */}
      <div
        className={`fixed z-50 top-0 left-0 h-full w-64 bg-white dark:bg-gray-800 text-white p-4 transition-transform transform ${isOpen ? 'translate-x-0' : '-translate-x-full'
          }`}
      >
        <h2 className="text-xl text-zwart font-roboto mb-4">{t('Instellingen')} </h2>
        <button
          className="absolute top-4 right-4 text-xl"
          onClick={toggleSidebar}
        >
          &times;
        </button>

        <div>
          <label className="block mb-2 text-zwart dark:text-white">{t('temperature')} Meting</label>
          <select className="p-1 bg-green text-gray-800">
            <option>Celcious</option>
            <option>Farhenheit</option>
            <option>Kelvin</option>

          </select>
        </div>

      {/* Settings Options */}
      <div>
        <label className="block mb-2 text-zwart dark:text-white">Dark Mode</label>
        <input type="checkbox" />
      </div>

      <div className="mt-4">
        <label className="block mb-2 text-zwart dark:text-white">Notifications</label>
        <input type="checkbox" />
      </div>

      <h1 className=' text-3xl' ></h1>

      <div className="mt-4">
        <label className="block mb-2 text-zwart dark:text-white">{t('temperature')}</label>
        <select className="p-1 bg-green text-gray-800">
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>

        </select>
      </div>
      <div className="mt-4">
        <label className="block mb-2 text-zwart dark:text-white">{t('Ph meter')}</label>
        <select className="p-1 bg-green text-gray-800">
          <option>2</option>
          <option>1</option>
          <option>3</option>
          <option>4</option>

        </select>
      </div>
      <div className="mt-4">
        <label className="block mb-2 text-zwart dark:text-white">{t('Troebelheid')}</label>
        <select className="p-1 bg-green text-gray-800">
          <option>3</option>
          <option>1</option>
          <option>2</option>
          <option>4</option>

        </select>
      </div>
      <div className="mt-4">
        <label className="block mb-2 text-zwart dark:text-white">{t('Zuurstof')}</label>
        <select className="p-1 bg-green text-gray-800">
          <option>4</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>

        </select>
      </div>
    </div>

      {/* Overlay when sidebar is open */ }
  {
    isOpen && (
      <div
        className="fixed inset-0 bg-black opacity-50"
        onClick={toggleSidebar}
      ></div>
    )
  }
    </div >
  );
};

export default Sidebar;
